import { Component, OnInit, ViewChild } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import {
  IonSlides,
  LoadingController,
  ModalController,
  NavController,
  ToastController,
} from '@ionic/angular';
import { plainToClass } from 'class-transformer';
import { BasePage } from 'src/app/base/base-page';
import { AdvertBean } from 'src/app/beans/advert-bean';
import { ArticleBean } from 'src/app/beans/article-bean';
import { ArticleCategoryBean } from 'src/app/beans/article-category-bean';
import { PagerBean } from 'src/app/beans/pager-bean';
import { ResponseBean } from 'src/app/beans/response-bean';
import { StatusContentComponent } from 'src/app/components/status-content/status-content.component';
import { ScreenFitService } from 'src/app/providers/screen-fit.service';
import { ThemeService } from 'src/app/providers/theme.service';
import { ApiService } from 'src/app/services/api.service';
import { RuntimeService } from 'src/app/services/runtime.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage extends BasePage implements OnInit {
  @ViewChild('appStatusContent', { static: false })
  appStatusContent: StatusContentComponent;

  @ViewChild('advertSlides', { static: false })
  advertSlides: IonSlides;

  advertList: AdvertBean[] = [];
  currentAdvertIndex: number = 0;
  hotArticleList: ArticleBean[] = [];
  categoryList: ArticleCategoryBean[] = [];
  currentCategory: ArticleCategoryBean = null;
  articleList: ArticleBean[] = [];
  pageIndex: number = 1;
  pageSize: number = 20;

  advertSlidesOption: any = {
    speed: 500,
    autoplay: {
      duration: 5000,
    },

    on: {
      beforeInit() {
        const swiper = this;
        swiper.classNames.push(`${swiper.params.containerModifierClass}fade`);
        const overwriteParams = {
          slidesPerView: 1,
          slidesPerColumn: 1,
          slidesPerGroup: 1,
          watchSlidesProgress: true,
          spaceBetween: 0,
          virtualTranslate: true,
        };
        swiper.params = Object.assign(swiper.params, overwriteParams);
        swiper.params = Object.assign(swiper.originalParams, overwriteParams);
      },

      setTranslate() {
        const swiper = this;
        const { slides } = swiper;
        for (let i = 0; i < slides.length; i += 1) {
          const $slideEl = swiper.slides.eq(i);
          const offset$$1 = $slideEl[0].swiperSlideOffset;
          let tx = -offset$$1;
          if (!swiper.params.virtualTranslate) tx -= swiper.translate;
          let ty = 0;
          if (!swiper.isHorizontal()) {
            ty = tx;
            tx = 0;
          }
          const slideOpacity = swiper.params.fadeEffect.crossFade
            ? Math.max(1 - Math.abs($slideEl[0].progress), 0)
            : 1 + Math.min(Math.max($slideEl[0].progress, -1), 0);
          $slideEl
            .css({
              opacity: slideOpacity,
            })
            .transform(`translate3d(${tx}px, ${ty}px, 0px)`);
        }
      },

      setTransition(duration: number) {
        const swiper = this;
        const { slides, $wrapperEl } = swiper;
        slides.transition(duration);
        if (swiper.params.virtualTranslate && duration !== 0) {
          let eventTriggered = false;
          slides.transitionEnd(() => {
            if (eventTriggered) return;
            if (!swiper || swiper.destroyed) return;
            eventTriggered = true;
            swiper.animating = false;
            const triggerEvents = ['webkitTransitionEnd', 'transitionend'];
            for (let i = 0; i < triggerEvents.length; i += 1) {
              $wrapperEl.trigger(triggerEvents[i]);
            }
          });
        }
      },
    },
  };

  constructor(
    protected loadingCtrl: LoadingController,
    protected toastCtrl: ToastController,
    protected runtime: RuntimeService,
    protected screenFitService: ScreenFitService,
    private navCtrl: NavController,
    private modalCtrl: ModalController,
    private api: ApiService,
    private router: Router,
    private themeService: ThemeService
  ) {
    super(loadingCtrl, toastCtrl, runtime, screenFitService);
  }

  ngOnInit() {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        if (event.url == '/home') {
          this.setPageTitle('Home');
        }
      }
    });
  }

  async initData() {
    // fetch data or load cache
    await Promise.all([
      this.fetchAdvertList(),
      this.fetchCategories(),
      this.fetchHotArticleList(),
      this.fetchArticleList(),
    ]);

    this.appStatusContent.invokeSuccess();
  }

  ionViewWillEnter() {
    this.setPageTitle('Home');
  }

  ionViewDidEnter() {
    (async () => {
      await this.initData();
    })().catch((error) => {
      console.error('[init data errror]', error);
      this.appStatusContent.invokeError();
    });
  }

  onRetry() {
    (async () => {
      await this.initData();
    })().catch((error) => {
      console.error('[init data errror]', error);
      this.appStatusContent.invokeError();
    });
  }

  back() {
    this.navCtrl.back();
  }

  onClickArticleItem(item: ArticleBean) {
    this.router.navigate(['/article-detail'], {
      queryParams: {
        uid: item.uid,
      },
    });
  }

  onClickAdvertTitle(index: number) {
    this.advertSlides.slideTo(index);
  }

  onClickCategoryItem(item: ArticleCategoryBean) {
    this.currentCategory = item;
    this.fetchArticleList();
  }

  onSlideChange(event: any) {
    this.currentAdvertIndex = event.target.swiper.activeIndex;
  }

  async fetchAdvertList() {
    const response: ResponseBean<PagerBean<AdvertBean>> = plainToClass(
      ResponseBean,
      await this.api.fetchAdvertList(null)
    );
    if (response.code == 200) {
      if (response.result.data && response.result.data.length > 0) {
        this.advertList = response.result.data;
      }
    } else {
      return Promise.reject({ code: response.code, message: response.message });
    }
  }

  async fetchCategories() {
    const response: ResponseBean<PagerBean<ArticleCategoryBean>> = plainToClass(
      ResponseBean,
      await this.api.fetchCategoryList(null)
    );
    if (response.code == 200) {
      if (response.result.data && response.result.data.length > 0) {
        this.categoryList = response.result.data;
        this.currentCategory = this.categoryList[0];
      }
    } else {
      return Promise.reject({ code: response.code, message: response.message });
    }
  }

  async fetchHotArticleList() {
    const response: ResponseBean<PagerBean<ArticleBean>> = plainToClass(
      ResponseBean,
      await this.api.fetchArticleList(null)
    );
    console.error(response);
    if (response.code == 200) {
      if (response.result.data && response.result.data.length > 0) {
        this.hotArticleList = response.result.data.slice(0, 3);
      }
    } else {
      return Promise.reject({ code: response.code, message: response.message });
    }
  }

  async fetchArticleList() {
    const response: ResponseBean<PagerBean<ArticleBean>> = plainToClass(
      ResponseBean,
      await this.api.fetchArticleList(null)
    );
    if (response.code == 200) {
      if (response.result.data && response.result.data.length > 0) {
        this.articleList = response.result.data;
      }
    } else {
      return Promise.reject({ code: response.code, message: response.message });
    }
  }
}
